import React ,{ useState }from "react";
import './Contacts.css';
import emailjs from '@emailjs/browser';
import img from '../../img/Location.png';
import { Button, TextField } from "@mui/material";
import MarkEmailReadRoundedIcon from '@mui/icons-material/MarkEmailReadRounded';
import TtyRoundedIcon from '@mui/icons-material/TtyRounded';
import CopyrightIcon from '@mui/icons-material/Copyright';
import SendIcon from '@mui/icons-material/Send';
import FacebookRoundedIcon from '@mui/icons-material/FacebookRounded';
import InstagramIcon from '@mui/icons-material/Instagram';
import WhatsAppIcon from '@mui/icons-material/WhatsApp';
import GitHubIcon from '@mui/icons-material/GitHub';


const Contacts = () =>{

    const [values ,setValues] = useState([]);
    const [error,setError]=useState([]);
    const [isSubmit,setIsSubmit]=useState(false);

    const submit = (e) =>{
        // e.preventDefault();
        setError(Validate(values));
        setIsSubmit(true);
        if( values.name && values.phone && values.email && values.subject && values.message)
        {
            var templateParams = {
                  name:values.name,
                  phone:values.phone,
                  email:values.email,
                  subject:values.subject,
                  message:values.message
               }
                emailjs.send('service_kp55o9z', 'template_9kxnnlg',templateParams, 'BWP5VoMuHTSLMvlS6')
                 .then((result) => {
                        setValues({name:"",phone:"",email:"",subject:"",message:""});
                    }, (error) => {
                        console.log(error.text);
                    });
                    alert("Message send successfully..");
                    setValues({name:"",phone:"",email:"",subject:"",message:""});
                    e.target.reset();
        }
        else
        {
            alert("Fill the fields");
        }
        e.target.reset();
    }

    // const sendEmail = (e) => {
    //     e.preventDefault();
    
    //     emailjs.sendForm('YOUR_SERVICE_ID', 'YOUR_TEMPLATE_ID', form.current, 'YOUR_PUBLIC_KEY')
    //       .then((result) => {
    //           console.log(result.text);
    //       }, (error) => {
    //           console.log(error.text);
    //       });
    //   };

    const onValidate = () =>{
        if(Object.keys(error).length !== 0)
        {
            setError(Validate(values));
            setIsSubmit(true);
        }
    }

    const Validate = (values) =>{
        const errors={};
        const refx =  /^[^\s@]+@[^\s@]+\.[^\s@]{2,}$/i;

        if(!values.name)
        {
            errors.name=" Field is required ..";
        }
        if(!values.phone)
        {
            errors.phone=" Field is required ..";
        }
        if(!values.email)
        {
            errors.email=" Field is required ..";
        }else if(!refx.test(values.email))
        {
            errors.email=" Incorrect email format ..";
        }
        if(!values.subject)
        {
            errors.subject=" Field is required ..";
        }
        if(!values.message)
        {
            errors.message=" Field is required ..";
        }

        return errors;

    }

    return(
        <div className="C-container" id="Contact">
{/* -------------  Left Side  ------------- */}
            <div className="C-left">
                <img src={img}/>
                <div className="C-content">
                    <h2>R,Kaliyappan.,BE( Computer Science )</h2>
                    <p style={{textDecoration:'underline'}}><MarkEmailReadRoundedIcon/>kalisoft117@gmail.com</p>
                    <p><TtyRoundedIcon/>9791745073 | 9626345073</p>
                    <br/>
                    <div>
                        <FacebookRoundedIcon/>
                        <GitHubIcon/>
                        <InstagramIcon/>
                        <WhatsAppIcon/>
                    </div>
                    <br/><hr style={{color:"lightgray",opacity:'30%'}}/>
                    <span>
                        <CopyrightIcon/><p> No Copyright Kaliyappan profile developed by KaliSanthosh softTech....</p>
                    </span>
                </div>
            </div>

{/* -------------  Right Side  ------------- */}

            <div className="C-right">
                 <div>
                   <div>
                    <TextField variant="outlined" required error={error.name} fullWidth size="small" label="Name" value={values['name']} onChange={(e)=>setValues({...values,'name':e.target.value})} onInput={onValidate}/><br/><br/>
                    <TextField variant="outlined" required error={error.phone} fullWidth size="small" label="Phone Number" value={values['phone']} onChange={(e)=>setValues({...values,'phone':e.target.value})} onInput={onValidate}/><br/><br/>
                    <TextField variant="outlined" required error={error.email} fullWidth size="small" label="email" value={values['email']} onChange={(e)=>setValues({...values,'email':e.target.value})} onInput={onValidate}/><br/><br/>
                    <TextField variant="outlined" required error={error.subject} fullWidth size="small" label="Subject" value={values['subject']} onChange={(e)=>setValues({...values,'subject':e.target.value})} onInput={onValidate}/><br/><br/>
                   </div>
                   <div>
                    <TextField multiline rows={7.5} variant="outlined" required error={error.message} fullWidth size="small" label="Say something" value={values['message']} onChange={(e)=>setValues({...values,'message':e.target.value})} onInput={onValidate}/><br/><br/>
                    <Button endIcon={<SendIcon/>} variant="contained" onClick={()=>submit()}>Send</Button>
                    </div>
                 </div>
            </div>
        </div>
    )
}
export default Contacts;